.footer {
    width: 100%;
    .container {
        width: 100%;
        max-width: 1130px;
        margin: 0 auto;
        padding: 0 20px;
        font-family: $charter-regular;
        color: #fff; }
    .col {
        width: calc(50% - 20px + 15px);
        display: inline-block;
        @include md {
            width: calc(100% - 20px + 15px);
            display: block; }
        .underlined {
            color: $gray;
            text-decoration: none;
            display: inline-block;
            padding-bottom: 1px;
            border-bottom: 1px solid $gray;
            &:hover {
                transition: 0.4s;
                border-bottom-color: rgba(225,225,0,0); }
            @include r(400) {
                border: none; } } }
    .footer__row-top {
        background-color: $brown;
        padding: 50px 0;
        .logo {
            display: block;
            margin-bottom: 40px; }
        .content {
            display: block;
            width: 100%;
            @include xs {
                font-size: em(14); }
            .address {
                color: $gray; }
            .footer__menu {
                .footer__menu-item {
                    padding-bottom: 20px;
                    .footer__menu-item__link {
                        font-family: $charter-bold;
                        color: #fff;
                        text-decoration: none;
                        &:hover {
                            transition: 0.4s;
                            color: $gray; } } } }
            .contact {
                .contact__department {
                    color: $gray; }
                .contact__info {
                    .underlined {
                        color: #fff; } } }
            .social {
                .icon-svg {
                    display: inline-block; }
                .icon-svg > * {
                    width: 44px;
                    height: 44px;
                    fill: $gray;
                    @include xs {
                        width: 35px;
                        height: 35px; }
                    &:hover {
                        transition: 0.4s;
                        fill: #fff; } } }
            .col__container {
                .col-inner {
                    width: calc(50% - 20px + 15px);
                    display: inline-block;
                    vertical-align: top;
                    @include r(400) {
                        width: 100%;
                        display: block; }
                    & > * {
                        margin-bottom: 27px; } } } } }


    .footer__row-btm {
        background-color: $dark-brown;
        padding: 25px 0;
        .col__text {
            font-size: em(12);
            color: $gray;
            @include sm {
                width: 100%;
                margin-bottom: 15px; } }
        .row {
            @include clr;
            .col {
                @include sm {
                    width: 100%; } }
            .col-left {
                width: calc(75% - 20px + 15px);
                float: left;
                .col-left__text {
                    display: inline-block;
                    vertical-align: top;
                    &:first-of-type {
                        width: calc(75%/2.3 - 20px + 15px); } } }
            .col-right {
                width: calc(25% - 20px + 15px);
                float: right; } } } }
