.header {
    width: 100%;
    height: 100vh;
    max-height: 1080px;
    padding: em(14);
    @include cover;
    .header__slider {
        width: 100%;
        height: 100%;
        border: 1px solid $gold;
        display: flex;
        flex-direction: column;
        position: relative;
        .logo {
            position: absolute;
            bottom: get-vh(400px);
            left: 50%;
            transform: translate(-50%);
            .logo__img {
                width: 250px;
                @include md {
                    width: 200px; }
                @include sm {
                    width: 150px; }
                @include xs {
                    width: 100px; } } }
        .titles {
            width: 100%;
            position: absolute;
            bottom: get-vh(190px);
            .title {
                font-size: em(48);
                color: #fff;
                text-align: center;
                @include md {
                    font-size: em(40); }
                @include sm {
                    font-size: em(32); }
                @include xs {
                    font-size: em(24); } } }
        .dots {
            text-align: center;
            bottom: get-vh(125px); }
        .scroll {
            width: 100%;
            color: #fff;
            position: absolute;
            bottom: get-vh(40px);
            .scroll__content {
                text-align: center;
                .scroll__text {
                    display: inline-block;
                    overflow: hidden;
                    font-size: em(14); }
                .scroll__icon {
                    display: inline-block;
                    padding: 0 10px;
                    .icon-mouse {
                        @include s(mouse); } } } } } }


